import React, { Component } from "react";
import axios from "axios";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { validationProvinsi } from "../../Validations/validationProvinsi";

const server = process.env.REACT_APP_BACKEND_URL;
const renderField = ({
  input,
  label,
  type,
  limit,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input {...input} type={type} className="form-control" maxLength={limit} />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
const maptoState = (state) => {
  return {
    limit: state.config.limit,
    error: state.alert.Error,
    editProvinsi: state.provinsi.editProvinsi,
  };
};
class AreaProvinsiKota extends Component {
  constructor(props) {
    super(props);
    this.state = {
      response: "",
      status: "AKTIF",
      listProvinsi: [],
      listKota: [],
      isiNama: "",
      isikode: "",
    };
  }

  componentDidMount() {
    axios
      .get(server + "provinces")
      .then((res) =>
        this.setState({
          listProvinsi: res.data,
        })
      )
      .catch((err) => console.log(err));

    axios
      .get(server + "citys")
      .then((res) =>
        this.setState({
          listKota: res.data,
        })
      )
      .catch((err) => console.log(err));
  }

  render() {
    var kodenya = this.props.kode;
    var namanya = this.props.nama;
    return (
      <div>
        <form onSubmit={this.props.handleSubmit}>
          <div className="row">
            <div className="col-md-2">
              <Field
                name={kodenya.replace(/\s/g, "")}
                type="text"
                normalize={(value) => value && value.toUpperCase()}
                component={renderField}
                limit={5}
                label={this.props.kode}
              />
            </div>
            {this.props.from === "kota" ? (
              <div className="col-md-4">
                <div className="form-group">
                  <label htmlFor="" className="text-black">
                    {this.props.kode_ext}
                  </label>
                  <Field
                    component="select"
                    name={
                      this.props.from === "kota" ? "kodeProvinsi" : "kodeKota"
                    }
                    type="text"
                    normalize={(value) => value && value.toUpperCase()}
                    className="form-control"
                    value="DEFAULT"
                  >
                    <option value="DEFAULT">SILAHKAN PILIH</option>
                    {this.state.listProvinsi.map((res) => (
                      <option value={res.kodeProvinsi} key={res.kodeProvinsi}>
                        {res.namaProvinsi}
                      </option>
                    ))}
                  </Field>
                </div>
              </div>
            ) : null}
            {/* AREA */}
            {this.props.from === "area" ? (
              <div className="col-md-4">
                <div className="form-group">
                  <label htmlFor="" className="text-black">
                    {this.props.kode_ext}
                  </label>
                  <Field
                    component="select"
                    name={
                      this.props.from === "kota" ? "kodeProvinsi" : "kodeKota"
                    }
                    type="text"
                    normalize={(value) => value && value.toUpperCase()}
                    className="form-control"
                  >
                    <option value="DEFAULT">SILAHKAN PILIH</option>
                    {this.state.listKota.map((res) => (
                      <option value={res.kodeKota} key={res.kodeKota}>
                        {res.namaKota}
                      </option>
                    ))}
                  </Field>
                </div>
              </div>
            ) : null}
            {/* Lokasi */}
            {this.props.from === "lokasi" ? (
              <div className="col-md-4">
                <Field
                  name="kodeExt"
                  type="text"
                  normalize={(value) => value && value.toUpperCase()}
                  component={renderField}
                  label={this.props.kode_ext}
                />
              </div>
            ) : null}
            <div className="col-md-4">
              <Field
                name={namanya.replace(/\s/g, "")}
                type="text"
                normalize={(value) => value && value.toUpperCase()}
                component={renderField}
                label={this.props.nama}
              />
            </div>
            <div className="col-md-2">
              <div className="form-group">
                <label htmlFor="" className="text-black">
                  Status
                </label>
                <Field
                  component="select"
                  name="status"
                  type="text"
                  normalize={(value) => value && value.toUpperCase()}
                  className="form-control"
                  value="AKTIF"
                >
                  <option value="AKTIF" key="AKTIF">
                    Aktif
                  </option>
                  <option value="NON-AKTIF" key="NON-AKTIF">
                    Non Aktif
                  </option>
                </Field>
              </div>
            </div>
            <div className="col-lg-12 col-md-12">
              <div className="text-right">
                <button
                  type="submit"
                  id="simpan"
                  className="btn btn-primary"
                  disabled={this.props.submitting}
                >
                  Submit
                </button>
              </div>
            </div>
          </div>
        </form>
      </div>
    );
  }
}

AreaProvinsiKota = reduxForm({
  form: "Provinsi", // a unique identifier for this form
  validate: validationProvinsi, // <--- validation function given to redux-form
  enableReinitialize: true,
})(AreaProvinsiKota);
export default connect(maptoState, null)(AreaProvinsiKota);
